"use client";

import React, { useState, useEffect, useRef } from 'react';
import { Store, Search, UserCircle, Star, ArrowLeft, LogIn, PlusCircle, ImageIcon, Video, Trash2, Edit, LogOut } from 'lucide-react';
import { auth, db, storage } from '../lib/firebase'; // Import centralized services
import { 
    createUserWithEmailAndPassword, 
    signInWithEmailAndPassword, 
    signOut, 
    onAuthStateChanged 
} from "firebase/auth";
import {
    collection,
    addDoc,
    doc,
    updateDoc,
    deleteDoc,
    query,
    where,
    onSnapshot,
    serverTimestamp,
    getDocs
} from "firebase/firestore";
import {
    ref as storageRef,
    uploadBytesResumable,
    getDownloadURL,
    deleteObject
} from "firebase/storage";


// --- UI COMPONENTS ---

const StarRating = ({ rating, reviewCount }) => (
    <div className="flex items-center">
        <div className="flex items-center">{[...Array(5)].map((_, i) => (<Star key={i} className={`h-4 w-4 ${i < Math.round(rating) ? 'text-yellow-400 fill-current' : 'text-gray-300'}`} />))}</div>
        {reviewCount !== undefined && <span className="ml-2 text-sm text-gray-500">({reviewCount})</span>}
    </div>
);

const Header = ({ setView, searchTerm, setSearchTerm, user }) => {
    const goHome = () => {
        setSearchTerm('');
        setView({ page: 'home' });
    }
    return (
        <header className="bg-white shadow-md sticky top-0 z-50">
            <nav className="container mx-auto px-4 py-3 flex justify-between items-center">
                <button onClick={goHome} className="flex items-center space-x-2"><Store className="h-8 w-8 text-blue-600" /><span className="text-2xl font-bold text-gray-800">ShopNext</span></button>
                <div className="flex-1 max-w-xl mx-4">
                    <form onSubmit={(e) => e.preventDefault()} className="relative">
                        <input type="text" value={searchTerm} onChange={(e) => setSearchTerm(e.target.value)} placeholder="Search for products, brands, and more" className="w-full py-2 pl-4 pr-12 text-gray-700 bg-gray-100 border border-gray-200 rounded-full focus:outline-none focus:ring-2 focus:ring-blue-500" />
                        <div className="absolute inset-y-0 right-0 flex items-center justify-center w-12 text-gray-500"><Search className="h-5 w-5" /></div>
                    </form>
                </div>
                <button onClick={() => setView({ page: 'vendor' })} className="flex items-center space-x-2 bg-gray-800 text-white px-4 py-2 rounded-full hover:bg-gray-900 transition-colors">
                    <UserCircle className="h-5 w-5" />
                    <span>{user ? 'My Dashboard' : 'Vendor Area'}</span>
                </button>
            </nav>
        </header>
    );
};

const Footer = () => (
    <footer className="bg-gray-800 text-white mt-12"><div className="container mx-auto px-4 py-6 text-center"><p>&copy; 2025 ShopNext. All rights reserved.</p><p className="text-sm text-gray-400">A prototype for educational purposes.</p></div></footer>
);

const ProductCard = ({ product, setView }) => (
    <div className="bg-white border rounded-lg shadow-sm overflow-hidden hover:shadow-lg transition-shadow duration-300 group">
        <div className="relative"><img src={product.imageUrl || 'https://placehold.co/600x400/e2e8f0/4a5568?text=Image'} alt={product.name} className="w-full h-48 object-cover" /><div className="absolute top-2 right-2 bg-white text-gray-800 px-2 py-1 rounded-full text-xs font-semibold">{product.vendor}</div></div>
        <div className="p-4">
            <h3 className="text-lg font-bold text-gray-800 truncate">{product.name}</h3>
            <p className="text-sm text-gray-500 mb-2 truncate">{product.subtitle}</p>
            <div className="flex justify-between items-center"><p className="text-xl font-extrabold text-blue-600">${parseFloat(product.price).toFixed(2)}</p><StarRating rating={product.rating} reviewCount={product.reviewCount} /></div>
            <button onClick={() => setView({ page: 'product', id: product.id })} className="mt-4 w-full bg-blue-500 text-white py-2 rounded-full font-semibold hover:bg-blue-600 transition-colors">View Details</button>
        </div>
    </div>
);

const HomePage = ({ products, setView }) => (
    <main className="container mx-auto px-4 py-8">
        <h2 className="text-3xl font-extrabold text-gray-900 mb-6">{products.length > 0 ? 'Featured Products' : 'No Products Found'}</h2>
        {products.length > 0 ? (<div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-6">{products.map(product => (<ProductCard key={product.id} product={product} setView={setView} />))}</div>) : (<div className="text-center py-16"><p className="text-gray-500 text-lg">We couldn't find any matches. Try a different search term.</p></div>)}
    </main>
);

// ReviewForm is defined in its own file now, so it will be imported into ProductPage.
// We'll define it again here for the single-file context to avoid breaking changes.
const ReviewFormComponent = ({ productId, user }) => {
    const [ratings, setRatings] = useState({ attention: 0, persuasion: 0, brandedRecall: 0, liking: 0 });
    const [text, setText] = useState('');
    const [error, setError] = useState('');
    const [success, setSuccess] = useState('');
    const criteria = ['attention', 'persuasion', 'brandedRecall', 'liking'];
    const handleRatingChange = (criterion, value) => { setRatings(prev => ({ ...prev, [criterion]: value })); };
    const updateProductRatingAfterSubmission = async () => {
         const reviewsRef = collection(db, "products", productId, "reviews");
         const reviewsSnapshot = await getDocs(reviewsRef);
         const newReviewCount = reviewsSnapshot.size;
         let totalRating = 0;
         reviewsSnapshot.forEach(doc => { totalRating += doc.data().overallRating; });
         const newAverageRating = newReviewCount > 0 ? totalRating / newReviewCount : 0;
         const productRef = doc(db, "products", productId);
         await updateDoc(productRef, { rating: newAverageRating, reviewCount: newReviewCount });
    };
    const handleReviewSubmit = async (e) => {
        e.preventDefault();
        setError(''); setSuccess('');
        if (Object.values(ratings).some(r => r === 0) || !text) { setError('Please provide a rating for all criteria and write a review.'); return; }
        const overallRating = Object.values(ratings).reduce((a, b) => a + b, 0) / criteria.length;
        try {
            await addDoc(collection(db, "products", productId, "reviews"), {
                userId: user.uid, username: user.email.split('@')[0], ...ratings,
                overallRating, text, createdAt: serverTimestamp()
            });
            await updateProductRatingAfterSubmission();
            setSuccess('Thank you for your review!');
            setRatings({ attention: 0, persuasion: 0, brandedRecall: 0, liking: 0 });
            setText('');
        } catch (err) { setError('Failed to submit review. ' + err.message); }
    };
    return (
        <div className="bg-white p-6 rounded-lg shadow-md border mt-8">
            <h3 className="text-xl font-bold text-gray-800 mb-4">Leave a Review</h3>
            <form onSubmit={handleReviewSubmit} className="space-y-4">
                {criteria.map(criterion => (<div key={criterion} className="flex flex-col sm:flex-row justify-between sm:items-center"><span className="capitalize text-gray-700 mb-2 sm:mb-0">{criterion.replace(/([A-Z])/g, ' $1')}</span><div className="flex">{[...Array(5)].map((_, i) => (<Star key={i} className={`h-6 w-6 cursor-pointer ${i < ratings[criterion] ? 'text-yellow-400 fill-current' : 'text-gray-300'}`} onClick={() => handleRatingChange(criterion, i + 1)} />))}</div></div>))}
                <div><textarea value={text} onChange={(e) => setText(e.target.value)} rows="4" placeholder="Share your thoughts..." className="w-full mt-2 p-2 border rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"></textarea></div>
                {error && <p className="text-sm text-red-500 text-center">{error}</p>}
                {success && <p className="text-sm text-green-500 text-center">{success}</p>}
                <button type="submit" className="w-full py-2 bg-blue-600 text-white rounded-full font-semibold hover:bg-blue-700">Submit Review</button>
            </form>
        </div>
    );
};


const ProductPage = ({ product, setView, user }) => {
    const [mainImage, setMainImage] = useState((product.images && product.images[0]) || product.imageUrl || 'https://placehold.co/600x400/e2e8f0/4a5568?text=Image');
    const [reviews, setReviews] = useState([]);
    useEffect(() => {
        const unsubscribe = onSnapshot(query(collection(db, "products", product.id, "reviews")), (snapshot) => {
            const fetchedReviews = [];
            snapshot.forEach(doc => fetchedReviews.push({ id: doc.id, ...doc.data() }));
            setReviews(fetchedReviews);
        });
        return () => unsubscribe();
    }, [product.id]);
    return (
        <main className="container mx-auto px-4 py-8">
            <button onClick={() => setView({ page: 'home' })} className="flex items-center text-blue-600 hover:underline mb-6"><ArrowLeft className="mr-2 h-4 w-4" /> Back to products</button>
            <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
                <div>
                    <img src={mainImage} alt={product.name} className="w-full rounded-lg shadow-md border" />
                    <div className="flex space-x-2 mt-4">{(product.images || []).map(img => <img key={img} src={img} onClick={() => setMainImage(img)} className={`w-20 h-20 object-cover rounded-md cursor-pointer border-2 ${mainImage === img ? 'border-blue-500' : 'border-transparent'}`} />)}</div>
                    {product.videoUrl && (<div className="mt-6"><h3 className="text-lg font-bold text-gray-800 mb-2">Product Video</h3><video src={product.videoUrl} controls className="w-full rounded-lg shadow-md border"></video></div>)}
                </div>
                <div>
                    <span className="text-sm font-semibold text-gray-500">{product.vendor}</span><h1 className="text-4xl font-extrabold text-gray-900 mt-1">{product.name}</h1><p className="text-lg text-gray-600 mt-2">{product.subtitle}</p><div className="my-4"><StarRating rating={product.rating} reviewCount={product.reviewCount} /></div><p className="text-3xl font-bold text-blue-600">${parseFloat(product.price).toFixed(2)}</p><div className="mt-6"><h3 className="text-lg font-bold text-gray-800">About this item</h3><p className="text-gray-600 mt-2">{product.longDescription}</p></div><div className="mt-4">{(product.tags || []).map(tag => <span key={tag} className="inline-block bg-gray-200 rounded-full px-3 py-1 text-sm font-semibold text-gray-700 mr-2 mb-2">#{tag}</span>)}</div>
                </div>
            </div>
            <div className="mt-12">
                <h2 className="text-2xl font-bold text-gray-800 mb-4">Customer Reviews</h2>
                {reviews.length > 0 ? (<div className="space-y-6">{reviews.map(review => (<div key={review.id} className="bg-white p-4 rounded-lg shadow-sm border"><div className="flex items-center mb-2"><StarRating rating={review.overallRating} /><span className="ml-4 font-bold text-gray-800">{review.username}</span></div><p className="text-gray-600">{review.text}</p></div>))}</div>) : (<p className="text-gray-500">No reviews yet. Be the first to share your thoughts!</p>)}
                {user && <ReviewFormComponent productId={product.id} user={user} />}
            </div>
        </main>
    );
};

const VendorLogin = () => {
    const [username, setUsername] = useState('');
    const [password, setPassword] = useState('');
    const [error, setError] = useState('');
    const formatEmail = (user) => `${user.trim()}@shopnext.dev`;
    const handleSignUp = async (e) => { e.preventDefault(); setError(''); if (!username || !password) { setError("Username and password cannot be empty."); return; } try { await createUserWithEmailAndPassword(auth, formatEmail(username), password); } catch (err) { setError(err.message); } };
    const handleSignIn = async (e) => { e.preventDefault(); setError(''); if (!username || !password) { setError("Username and password cannot be empty."); return; } try { await signInWithEmailAndPassword(auth, formatEmail(username), password); } catch (err) { setError(err.message); } };
    return (<div className="flex items-center justify-center min-h-[60vh]"><div className="w-full max-w-md p-8 space-y-6 bg-white rounded-lg shadow-md border"><h2 className="text-2xl font-bold text-center text-gray-800">Vendor Login</h2><form className="space-y-4"><div><label className="text-sm font-medium text-gray-700">Username</label><input type="text" value={username} onChange={(e) => setUsername(e.target.value)} placeholder="e.g., ElectroGadgets" className="w-full px-4 py-2 mt-2 border rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500" /></div><div><label className="text-sm font-medium text-gray-700">Password</label><input type="password" value={password} onChange={(e) => setPassword(e.target.value)} placeholder="••••••••" className="w-full px-4 py-2 mt-2 border rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500" /></div>{error && <p className="text-sm text-red-500 text-center">{error}</p>}<div className="flex space-x-2"><button onClick={handleSignIn} className="w-full py-3 font-semibold text-white bg-blue-600 rounded-full hover:bg-blue-700 transition-colors flex items-center justify-center"><LogIn className="h-5 w-5 mr-2"/>Sign In</button><button onClick={handleSignUp} className="w-full py-3 font-semibold text-blue-600 bg-blue-100 rounded-full hover:bg-blue-200 transition-colors">Sign Up</button></div></form><p className="text-xs text-center text-gray-500">For this demo, you can Sign Up to create an account.</p></div></div>);
};

const CreateProductForm = ({ setVendorView, user, editingProduct }) => {
    const [product, setProduct] = useState({ name: '', subtitle: '', shortDescription: '', longDescription: '', price: '', category: '', tags: '' });
    const [imageUploadProgress, setImageUploadProgress] = useState(0); const [videoUploadProgress, setVideoUploadProgress] = useState(0);
    const [imageUrl, setImageUrl] = useState(''); const [videoUrl, setVideoUrl] = useState('');
    const [isUploading, setIsUploading] = useState(false); const [error, setError] = useState(''); const [success, setSuccess] = useState('');
    const imageInputRef = useRef(null); const videoInputRef = useRef(null);
    useEffect(() => { if (editingProduct) { setProduct({ name: editingProduct.name || '', subtitle: editingProduct.subtitle || '', shortDescription: editingProduct.shortDescription || '', longDescription: editingProduct.longDescription || '', price: editingProduct.price || '', category: editingProduct.category || '', tags: (editingProduct.tags || []).join(', ') }); setImageUrl(editingProduct.imageUrl || ''); setVideoUrl(editingProduct.videoUrl || ''); } else { setProduct({ name: '', subtitle: '', shortDescription: '', longDescription: '', price: '', category: '', tags: '' }); setImageUrl(''); setVideoUrl(''); } }, [editingProduct]);
    const handleFileChange = (e, fileType) => { if (e.target.files[0]) { handleFileUpload(e.target.files[0], fileType); } };
    const handleFileUpload = (file, fileType) => { if (!file) return; setIsUploading(true); const folder = fileType === 'image' ? 'products' : 'product-videos'; const fileRef = storageRef(storage, `${folder}/${Date.now()}_${file.name}`); const uploadTask = uploadBytesResumable(fileRef, file); uploadTask.on('state_changed', (snapshot) => { const progress = (snapshot.bytesTransferred / snapshot.totalBytes) * 100; if (fileType === 'image') setImageUploadProgress(progress); else setVideoUploadProgress(progress); }, (error) => { setError(`Upload failed: ${error.message}`); setIsUploading(false); }, () => { getDownloadURL(uploadTask.snapshot.ref).then((downloadURL) => { if (fileType === 'image') setImageUrl(downloadURL); else setVideoUrl(downloadURL); setIsUploading(false); }); }); };
    const handleChange = (e) => { const { name, value } = e.target; setProduct(prev => ({ ...prev, [name]: value })); };
    const handleSaveProduct = async (e) => {
        e.preventDefault(); setError(''); setSuccess('');
        if (!product.name || !product.price || !product.category) { setError('Product Name, Price, and Category are required.'); return; }
        try {
            const commonData = { ...product, price: parseFloat(product.price), tags: product.tags.split(',').map(tag => tag.trim()).filter(t => t), imageUrl: imageUrl, videoUrl: videoUrl, images: imageUrl ? [imageUrl] : [] };
            if (editingProduct) { await updateDoc(doc(db, "products", editingProduct.id), commonData); setSuccess('Product updated successfully!'); } 
            else { await addDoc(collection(db, "products"), { ...commonData, vendorId: user.uid, vendor: user.email.split('@')[0], createdAt: serverTimestamp(), rating: 0, reviewCount: 0 }); setSuccess('Product saved successfully!'); }
            setTimeout(() => { setVendorView('dashboard'); }, 1500);
        } catch (err) { setError('Failed to save product. ' + err.message); }
    };
    return (<div className="bg-white p-8 rounded-lg shadow-md border max-w-2xl mx-auto"><h3 className="text-xl font-bold mb-6 text-gray-800">{editingProduct ? 'Edit Product' : 'Add New Product'}</h3><form onSubmit={handleSaveProduct} className="space-y-6">{/* Form fields... */}{/* Omitted for brevity, they are the same as before */}<div><label className="block text-sm font-medium text-gray-700">Product Name</label><input name="name" value={product.name} onChange={handleChange} type="text" className="mt-1 block w-full border rounded-md p-2"/></div><div><label className="block text-sm font-medium text-gray-700">Subtitle</label><input name="subtitle" value={product.subtitle} onChange={handleChange} type="text" className="mt-1 block w-full border rounded-md p-2"/></div><div><label className="block text-sm font-medium text-gray-700">Long Description</label><textarea name="longDescription" value={product.longDescription} onChange={handleChange} rows="3" className="mt-1 block w-full border rounded-md p-2"></textarea></div><div className="grid grid-cols-2 gap-4"><div><label>Price</label><input name="price" value={product.price} onChange={handleChange} type="number" step="0.01" className="mt-1 block w-full border rounded-md p-2"/></div><div><label>Category</label><input name="category" value={product.category} onChange={handleChange} type="text" className="mt-1 block w-full border rounded-md p-2"/></div></div><div><label>Tags</label><input name="tags" value={product.tags} onChange={handleChange} type="text" className="mt-1 block w-full border rounded-md p-2"/></div><div><p className="block text-sm font-medium text-gray-700 mb-2">Product Media</p><div className="flex space-x-4"><input type="file" ref={imageInputRef} onChange={(e) => handleFileChange(e, 'image')} className="hidden" accept="image/*" /><button type="button" onClick={() => imageInputRef.current.click()} disabled={isUploading} className="flex items-center justify-center w-full py-2 px-4 border border-dashed rounded-md"> <ImageIcon className="h-5 w-5 mr-2"/>Upload Photo</button><input type="file" ref={videoInputRef} onChange={(e) => handleFileChange(e, 'video')} className="hidden" accept="video/*" /><button type="button" onClick={() => videoInputRef.current.click()} disabled={isUploading} className="flex items-center justify-center w-full py-2 px-4 border border-dashed rounded-md"><Video className="h-5 w-5 mr-2"/>Upload Video</button></div>{imageUploadProgress > 0 && <div className="mt-2"><div className="w-full bg-gray-200 rounded-full h-2.5"><div className="bg-blue-600 h-2.5 rounded-full" style={{ width: `${imageUploadProgress}%` }}></div></div></div>}{videoUploadProgress > 0 && <div className="mt-2"><div className="w-full bg-gray-200 rounded-full h-2.5"><div className="bg-green-600 h-2.5 rounded-full" style={{ width: `${videoUploadProgress}%` }}></div></div></div>}<div className="flex space-x-4">{imageUrl && <img src={imageUrl} className="mt-4 w-32 h-32 object-cover rounded-md"/>}{videoUrl && <video src={videoUrl} controls className="mt-4 w-48 object-cover rounded-md"/>}</div></div>{error && <p className="text-sm text-red-500 text-center">{error}</p>}{success && <p className="text-sm text-green-500 text-center">{success}</p>}<div className="flex justify-end space-x-4"><button type="button" onClick={() => setVendorView('dashboard')} className="px-6 py-2 border rounded-full">Cancel</button><button type="submit" disabled={isUploading} className="px-6 py-2 bg-blue-600 text-white rounded-full">{isUploading ? 'Uploading...' : 'Save Product'}</button></div></form></div>);
};

const VendorDashboard = ({ setVendorView, user, setView, onEditProduct }) => {
    const [vendorProducts, setVendorProducts] = useState([]);
    useEffect(() => { if (user) { const q = query(collection(db, "products"), where("vendorId", "==", user.uid)); const unsubscribe = onSnapshot(q, (snapshot) => { const prods = []; snapshot.forEach((doc) => prods.push({ id: doc.id, ...doc.data() })); setVendorProducts(prods); }); return () => unsubscribe(); } }, [user]);
    const handleDeleteProduct = async (product) => { try { await deleteDoc(doc(db, "products", product.id)); if (product.imageUrl) await deleteObject(storageRef(storage, product.imageUrl)); if (product.videoUrl) await deleteObject(storageRef(storage, product.videoUrl)); } catch (error) { console.error("Error deleting product: ", error); } };
    const handleSignOut = async () => { await signOut(auth); setView({ page: 'home' }); };
    return (<div><div className="flex justify-between items-center mb-6"><div><h2 className="text-2xl font-bold text-gray-800">My Products ({vendorProducts.length})</h2><p className="text-sm text-gray-500">Logged in as: {user?.email.split('@')[0]}</p></div><div className="flex items-center"><button onClick={() => { onEditProduct(null); setVendorView('create');}} className="flex items-center bg-blue-600 text-white px-4 py-2 rounded-full font-semibold hover:bg-blue-700 transition-colors mr-4"><PlusCircle className="h-5 w-5 mr-2" />Add New Product</button><button onClick={handleSignOut} className="flex items-center text-gray-600 hover:text-red-500"><LogOut className="h-5 w-5 mr-2" />Sign Out</button></div></div><div className="bg-white rounded-lg shadow-md border overflow-hidden"><table className="min-w-full divide-y divide-gray-200"><thead className="bg-gray-50"><tr><th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase">Product</th><th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase">Price</th><th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase">Rating</th><th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase">Actions</th></tr></thead><tbody className="bg-white divide-y divide-gray-200">{vendorProducts.map(p => (<tr key={p.id}><td className="px-6 py-4"><div className="flex items-center"><img className="h-10 w-10 rounded-md object-cover mr-4" src={p.imageUrl} alt={p.name} /><span className="font-medium text-gray-900">{p.name}</span></div></td><td className="px-6 py-4 text-gray-600">${parseFloat(p.price).toFixed(2)}</td><td className="px-6 py-4"><StarRating rating={p.rating} reviewCount={p.reviewCount} /></td><td className="px-6 py-4"><div className="flex space-x-4"><button onClick={() => onEditProduct(p)} className="text-blue-600 hover:text-blue-800"><Edit className="h-5 w-5" /></button><button onClick={() => handleDeleteProduct(p)} className="text-red-600 hover:text-red-800"><Trash2 className="h-5 w-5" /></button></div></td></tr>))}</tbody></table></div></div>);
};

const VendorArea = ({ setView, user }) => {
    const [vendorView, setVendorView] = useState('dashboard'); const [editingProduct, setEditingProduct] = useState(null);
    const handleEditProduct = (product) => { setEditingProduct(product); setVendorView('create'); };
    const handleFormClose = () => { setEditingProduct(null); setVendorView('dashboard'); };
    const renderContent = () => { if (!user) return <VendorLogin />; switch (vendorView) { case 'create': return <CreateProductForm setVendorView={handleFormClose} user={user} editingProduct={editingProduct} />; case 'dashboard': default: return <VendorDashboard setVendorView={setVendorView} user={user} setView={setView} onEditProduct={handleEditProduct} />; } };
    return (<main className="container mx-auto px-4 py-8">{renderContent()}</main>);
};


// --- MAIN APP COMPONENT ---

export default function App() {
  const [view, setView] = useState({ page: 'home', id: null });
  const [searchTerm, setSearchTerm] = useState('');
  const [allProducts, setAllProducts] = useState([]);
  const [filteredProducts, setFilteredProducts] = useState([]);
  const [user, setUser] = useState(null); 
  const [loading, setLoading] = useState(true); 

  useEffect(() => { const unsubscribe = onAuthStateChanged(auth, (currentUser) => { setUser(currentUser); setLoading(false); }); return () => unsubscribe(); }, []);
  useEffect(() => { const unsubscribe = onSnapshot(collection(db, "products"), (snapshot) => { const prods = []; snapshot.forEach((doc) => prods.push({ id: doc.id, ...doc.data() })); setAllProducts(prods); }); return () => unsubscribe(); }, []);
  useEffect(() => {
      if (searchTerm.trim() === '') { setFilteredProducts(allProducts); } 
      else { const lowercasedQuery = searchTerm.toLowerCase(); const results = allProducts.filter(p => p.name.toLowerCase().includes(lowercasedQuery) || (p.subtitle && p.subtitle.toLowerCase().includes(lowercasedQuery)) || (p.tags && p.tags.some(t => t.toLowerCase().includes(lowercasedQuery))) || (p.vendor && p.vendor.toLowerCase().includes(lowercasedQuery)) || (p.shortDescription && p.shortDescription.toLowerCase().includes(lowercasedQuery))); setFilteredProducts(results); }
      if (searchTerm.trim() !== '') { setView({ page: 'home', id: null }); }
  }, [searchTerm, allProducts]);

  const renderView = () => {
    switch (view.page) {
      case 'product': const product = allProducts.find(p => p.id === view.id); return product ? <ProductPage product={product} setView={setView} user={user} /> : <HomePage products={filteredProducts} setView={setView} />;
      case 'vendor': return <VendorArea setView={setView} user={user} />;
      case 'home': default: return <HomePage products={filteredProducts} setView={setView} />;
    }
  };

  if (loading) { return <div className="min-h-screen flex items-center justify-center text-lg">Loading Application...</div> }

  return (
    <div className="bg-gray-50 min-h-screen font-sans">
        <Header setView={setView} searchTerm={searchTerm} setSearchTerm={setSearchTerm} user={user} />
        {renderView()}
        <Footer />
    </div>
  );
}